.header {
    text-align: center;
    background: #000;
    color: #fff;
    font-size: 10px;
}

.table {
    width: 90%;
    /*max-width: 320px;*/
    margin: 10px auto 80px;
}

@media (min-width: 370px) {
    .table {
        font-size: 1.15em;
    }
}

@media (min-width: 992px) {
    main {
        display: flex;
    }

    .table {
        grid-area: table;
        max-width: 600px;
    }
}

.tableHeader {
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    margin-bottom: 10px;
    border-bottom: solid 1px grey;
}

.tableHeader .table__title {
    flex: 0 1 100%;
    text-align: center;
    margin-bottom: 10px;
}

.tableHeader span {
    display: block;
    text-transform: capitalize;
    font-weight: bold;
    font-size: 1.1em;
}

.tableHeader [class*='timeHeader'] {
    text-decoration: underline;
    align-self: flex-end;
    cursor: pointer;
}

.timeRenderer {
    display: none;
}

.mobileNav {
    display: block;
}

@media(min-width: 992px) {
    .mobileNav {
        display: none;
    }
}

.desktopNav {
    display: none;
}

@media(min-width: 992px) {
    .desktopNav {
        display: block;
    }
}